.form-falla {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.form-falla__block {
  background-color: #f9fafb;
  border-radius: 18px;
  padding: 12px 14px;
  border: 1px solid #e5e7eb;
}
.form-falla__grid-2 {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.form-falla__actions {
  margin-top: 12px;
  display: flex;
  justify-content: flex-end;
}

@media (min-width: 768px) {
  .form-falla__grid-2 {
    display: grid;
    grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
    gap: 14px;
  }
}
.field {
  position: relative;
  display: flex;
  flex-direction: column;
}

.field__input {
  width: 100%;
  padding: 14px 12px;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
  background-color: #ffffff;
  font-size: 0.95rem;
  color: #111827;
  line-height: 1.3;
  outline: none;
  transition: border-color 0.15s ease, box-shadow 0.15s ease, background-color 0.15s ease;
}
.field__input::placeholder {
  color: transparent;
}
.field__input:focus {
  border-color: #2563eb;
  box-shadow: 0 18px 40px rgba(15, 23, 42, 0.12);
  background-color: #ffffff;
}

.field__textarea {
  padding-top: 18px;
  min-height: 140px;
  resize: vertical;
}

/* Label flotante */
.field__label {
  position: absolute;
  left: 14px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 0.9rem;
  color: #9ca3af;
  pointer-events: none;
  background-color: transparent;
  transition: 0.15s ease;
}

textarea.field__input + .field__label {
  top: 18px;
}

/* “Flotado” cuando hay contenido o focus */
.field__input:not(:placeholder-shown) + .field__label,
.field__input:focus + .field__label,
textarea.field__input:not(:placeholder-shown) + .field__label,
textarea.field__input:focus + .field__label {
  top: 6px;
  font-size: 0.7rem;
  color: #2563eb;
  transform: translateY(0);
}

.media-card {
  display: grid;
  gap: 6px;
}
.media-card__header {
  display: grid;
