}
.media-card__title {
  margin: 0;
  font-size: 0.9rem;
  font-weight: 600;
  color: #111827;
}
.media-card__subtitle {
  margin: 0;
  font-size: 0.8rem;
  color: #9ca3af;
}
.media-card__preview {
  margin-top: 6px;
  border-radius: 14px;
  border: 1px solid #e5e7eb;
  background-color: #ffffff;
  padding: 10px;
}
.media-card__image, .media-card__video {
  display: block;
