  color: #9ca3af;
}
.media-card__filename {
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}

.uploader {
  position: relative;
  border-radius: 14px;
  border: 1px dashed #e5e7eb;
  background-color: #fff;
