.form-usuario {
  max-width: 980px;
  margin: 0 auto;
  display: grid;
  gap: 12px;
  grid-template-columns: repeat(2, minmax(0, 1fr));
}
.form-usuario .contenido-bloque {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 18px;
  box-shadow: 0 10px 24px rgba(15, 23, 42, 0.08);
  padding: 12px 14px;
}
.form-usuario .activo-falla,
.form-usuario .biblioteca-falla {
  grid-column: span 1;
}
.form-usuario__actions {
  grid-column: 1/-1;
  display: flex;
  justify-content: flex-end;
  margin-top: 8px;
}

.field__textarea {
  min-height: 120px;
  padding-top: 20px;
  resize: vertical;
}

.field__select {
  appearance: none;
  padding-right: 40px;
  background-image: linear-gradient(45deg, transparent 50%, #9ca3af 50%), linear-gradient(135deg, #9ca3af 50%, transparent 50%);
  background-position: calc(100% - 18px) calc(50% - 2px), calc(100% - 12px) calc(50% - 2px);
  background-size: 6px 6px, 6px 6px;
  background-repeat: no-repeat;
}

.switch {
  display: flex;
  align-items: center;
  gap: 10px;
}

.switch__input {
  width: 42px;
  height: 24px;
  appearance: none;
  background: #e5e7eb;
  border-radius: 999px;
  position: relative;
  cursor: pointer;
  border: 1px solid #d1d5db;
  transition: background-color 0.2s ease, border-color 0.2s ease;
}
.switch__input::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 3px;
  width: 18px;
  height: 18px;
  transform: translateY(-50%);
  background: #ffffff;
  border-radius: 50%;
  box-shadow: 0 6px 16px rgba(15, 23, 42, 0.18);
  transition: left 0.2s ease;
}
.switch__input:checked {
  background: #2563eb;
  border-color: rgb(22.0514285714, 88.3028571429, 233.6285714286);
}
.switch__input:checked::after {
  left: 20px;
}

.switch__label {
  display: flex;
  flex-direction: column;
  gap: 2px;
  cursor: pointer;
}

.switch__title {
  font-size: 0.9rem;
  font-weight: 600;
  color: #111827;
}

@media (max-width: 860px) {
  .form-falla {
    grid-template-columns: 1fr;
  }
  .form-falla .correo-falla,
  .form-falla .rol-falla,
  .form-falla .activo-falla,
  .form-falla .biblioteca-falla {
    grid-column: 1/-1;
  }
}